.albums-page-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background-color: rgb(221, 255, 244);
}

.albums-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 20px;
    width: 100%;
}

.pagination-bar{
    width: 100%;
    height: 20px;
    display: flex;
    justify-content: center;
}

.pagination-button{
    cursor: pointer;
    width: 50px;
    color: blue;
    text-decoration: underline;
}